
.enroll {
  position: relative;
}
.enroll .swiper-wrraper {
    width: 870px;
    height: 348px;
    overflow: hidden;
    position: relative;
}
.enroll .swiper-wrraper .swiper-pagination {
      position: absolute;
      display: inline-block;
      bottom: 50px;
      left: 655px;
      width: 30%;
}
.enroll .screach-wrapper {
    margin-bottom: 30px;
}
.enroll .screach-wrapper .top_content {
      height: 65px;
      width: 870px;
      background: #efefef;
      font-size: 0;
}
.enroll .screach-wrapper .top_content .title {
        display: inline-block;
        vertical-align: top;
        font-size: 17px;
        line-height: 65px;
        margin-left: 158px;
        margin-right: 15px;
        color: #ff6633;
}
.enroll .screach-wrapper .top_content .text {
        display: inline-block;
        width: 374px;
        height: 42px;
        background-color: #fff;
        margin-top: 12px;
        font-size: 12px;
        text-indent: 2em;
        color: #adadad;
        border-radius: 2px;
        margin-right: 5px;
}
.enroll .screach-wrapper .top_content .button {
        display: inline-block;
        padding: 15px;
        color: #fff;
        background: #ef6224;
        vertical-align: top;
        margin-top: 12px;
        font-size: 12px;
        margin-right: 23px;
}
.enroll .screach-wrapper .top_content .pro {
        display: inline-block;
        font-size: 12px;
        cursor: pointer;
}
.enroll .screach-wrapper .slow-enter-active,
    .enroll .screach-wrapper .slow-leave-active {
      -webkit-transition: opacity .5s;
      transition: opacity .5s;
}
.enroll .screach-wrapper .slow-enter,
    .enroll .screach-wrapper .slow-leave-active {
      opacity: 0;
}
.enroll .screach-wrapper .bottom-content {
      background: #f6f6f6;
      height: 120px;
      width: 870px;
      padding-left: 159px;
      padding-top: 16px;
      box-sizing: border-box;
}
.enroll .screach-wrapper .bottom-content .area {
        height: 22px;
        line-height: 22px;
        font-size: 11px;
        margin-bottom: 10px;
}
.enroll .screach-wrapper .bottom-content .area span {
          padding: 5px 12px;
          vertical-align: top;
          cursor: pointer;
}
.enroll .screach-wrapper .bottom-content .area span.active {
            background: #dd8649;
}
.enroll .screach-wrapper .bottom-content .time {
        height: 22px;
        line-height: 22px;
        font-size: 11px;
        margin-bottom: 10px;
}
.enroll .screach-wrapper .bottom-content .time span {
          padding: 5px 12px;
          vertical-align: top;
          cursor: pointer;
}
.enroll .screach-wrapper .bottom-content .time span.active {
            background: #f5b849;
}
.enroll .screach-wrapper .bottom-content .type {
        height: 22px;
        line-height: 22px;
        font-size: 11px;
        margin-bottom: 10px;
}
.enroll .screach-wrapper .bottom-content .type span {
          padding: 5px 12px;
          vertical-align: top;
          cursor: pointer;
}
.enroll .screach-wrapper .bottom-content .type span.active {
            background: #72c2b1;
}
.enroll .screach-wrapper .bottom-content.active {
        height: 0;
}
