
#app .inner {
  width: 1200px;
  margin: 0 auto;
}
#app .inner .nav {
    height: 80px;
    background: #fff;
    font-size: 0;
}
#app .inner .nav .logo {
      display: inline-block;
      vertical-align: top;
      width: 112px;
      height: 38px;
      margin-top: 23px;
      margin-right: 59px;
      background: url("./assets/logo.png");
      background-size: cover;
}
#app .inner .nav .item {
      display: inline-block;
      height: 17px;
      line-height: 80px;
      font-size: 17px;
      margin-right: 28px;
}
#app .inner .nav .item .title {
        color: #87a5b3;
}



