
.re {
  position: relative;
}
.ab {
  position: absolute;
}
i,
em {
  font-style: normal;
}
.equip {
  width: 1200px;
  height: 100%;
}
.equip .equip_left {
    width: 840px;
    height: 100%;
    float: left;
}
.equip .equip_left .equip_flash {
      width: 840px;
      height: 360px;
      overflow: hidden;
      position: relative;
}
.equip .equip_left .equip_flash p.title {
        position: absolute;
        bottom: 0;
        height: 50px;
        width: 100%;
        background: rgba(0, 0, 0, 0.4);
        text-indent: 1em;
        line-height: 50px;
        font-size: 18px;
        color: #fff;
}
.equip .equip_left .equip_flash .swiper-button-prev {
        position: absolute;
        top: 172px;
        left: 10px;
}
.equip .equip_left .equip_flash .swiper-button-next {
        position: absolute;
        top: 172px;
        right: 10px;
}
.equip .equip_left h2 {
      width: 100%;
      height: 52px;
      line-height: 52px;
      font-size: 26px;
      color: #5e5454;
      margin-top: 30px;
      border-bottom: 4px solid #eeeeee;
}
.equip .equip_left .equip_left_list {
      width: 100%;
      height: 100%;
}
.equip .equip_left .equip_left_list li {
        width: 840px;
        height: 230px;
        margin: 30px 0;
        border-bottom: 1px solid #d9dcdf;
}
.equip .equip_left .equip_left_list li div {
          height: 200px;
}
.equip .equip_left .equip_left_list li .equip_left_list_img {
          width: 360px;
          float: left;
}
.equip .equip_left .equip_left_list li .equip_left_list_img p {
            padding: 0 10px;
            height: 22px;
            font-size: 12px;
            line-height: 22px;
            background: #e74e34;
            color: #fff;
            top: 0;
            left: 0;
}
.equip .equip_left .equip_left_list li .equip_left_list_art {
          width: 450px;
          height: 200px;
          float: right;
          font-size: 12px;
          color: #695f5d;
}
.equip .equip_left .equip_left_list li .equip_left_list_art a {
            color: #000;
}
.equip .equip_left .equip_left_list li .equip_left_list_art h3 {
            width: 100%;
            height: 32px;
            line-height: 32px;
            text-align: left;
            font-size: 20px;
}
.equip .equip_left .equip_left_list li .equip_left_list_art .equip_left_list_art_title {
            width: 100%;
            height: 24px;
            line-height: 24px;
            margin-bottom: 6px;
}
.equip .equip_left .equip_left_list li .equip_left_list_art .equip_left_list_art_title a {
              font-size: 14px;
              color: #0e90d2;
}
.equip .equip_left .equip_left_list li .equip_left_list_art .equip_left_list_art_title b {
              color: #8f8f8f;
}
.equip .equip_left .equip_left_list li .equip_left_list_art .equip_left_list_art_text {
            font-size: 14px;
            line-height: 24px;
}
.equip .equip_right {
    width: 296px;
    height: 100%;
    float: right;
}
.equip .equip_right li {
      width: 100%;
      height: 384px;
      border-bottom: 1px solid #979797;
      font-size: 14px;
      line-height: 24px;
      margin-top: 20px;
}
.equip .equip_right li a {
        color: #2c2c2c;
}
.equip .equip_right li span {
        font-size: 12px;
        color: #fff;
        display: inline-block;
        width: 54px;
        height: 18px;
        background: #ae71cd;
        text-align: center;
        line-height: 18px;
}
.equip .equip_right li p {
        color: #ff0000;
}
.equip .equip_right li p em {
          color: #2c2c2c;
}
.equip .equip_right li p i {
          font-weight: bold;
}
.equip .equip_right li .equip_right_img {
        width: 296px;
        text-align: center;
        display: table-cell;
        vertical-align: middle;
}
.equip .equip_right li .equip_right_img img {
          width: 200px;
          height: 200px;
}
.equip .equip_right li .equip_right_buy {
        display: block;
        width: 92px;
        height: 34px;
        background: #99cc66;
        border-radius: 4px;
        color: #fff;
        text-align: center;
        line-height: 34px;
        margin-top: 20px;
        margin-left: 100px;
}



