
.column .c3left {
  width: 830px;
  float: left;
}
.column .c3list {
  position: relative;
}
.column .c3list img {
    width: 100%;
    margin-bottom: 25px;
}
.column .c3list h3 {
    position: absolute;
    z-index: 100;
    top: 38px;
    left: 41px;
    margin: 0;
    font-size: 50px;
    color: #fff;
}
.column .c3listt {
  border-bottom: 1px solid #d9dcdf;
  width: 100%;
  height: 0;
  margin: 0 0 25px 0;
}
.column .c3listh {
  position: relative;
  font-size: 12px;
  color: #8f8f8f;
  margin-bottom: 25px;
}
.column .c3listh img {
    float: left;
    margin-right: 18px;
    width: 360px;
    height: 200px;
}
.column .c3listh h3 {
    font-size: 20px;
    color: #2c2c2c;
    margin: 0;
    line-height: 30px;
    padding: 0 0 6px 0;
    font-weight: 400;
    height: 32px;
    overflow: hidden;
}
.column .c3listh h3:hover {
    color: #0aa3da;
}
.column .c3listh span {
    padding-right: 10px;
    font-size: 14px;
    color: #555;
}
.column .c3listh span a {
      background-color: transparent;
      color: #0e90d2;
}
.column .c3listh span a:hover {
      color: #095f8a;
}
.column .c3listh p {
    color: #695f5d;
    line-height: 24px;
    margin: 8px 0 0 0;
    overflow: hidden;
    font-size: 14px;
    min-height: 140px;
}
.column .newsmore {
  text-align: center;
  cursor: pointer;
  padding: 8px;
  background: #fff8c7;
  border: 1px solid #f4e78a;
  color: #117fb9;
  font-size: 12px;
  line-height: 1;
}
.column .c3right {
  float: right;
  width: 340px;
  text-align: center;
  _padding: 22px 20px 0 20px;
}
.column .c3rig {
  text-align: left;
  color: #695f5d;
  padding-bottom: 24px;
  border-bottom: 1px solid #979797;
  margin-bottom: 24px;
}
.column .c3rig a {
    color: #2c2c2c;
}
.column .c3rig span {
    color: #ff0000;
    font-weight: bold;
}
.column .c3rig img {
    display: block;
    width: 200px;
    margin: 18px auto;
}
.column .c3rig i {
    font-style: normal;
}
.column .c3rig .c3righ {
  color: #ffffff;
  width: 92px;
  height: 34px;
  line-height: 34px;
  text-align: center;
  background: #87bb54;
  border-radius: 3px;
  margin: 0 auto;
  display: block;
}



